import React, { useState } from "react";
import "./Inner.scss";
import Box from "../../components/Box/Box";
import TimeLine from "./../../components/TimeLine/TimeLine";
import Expertise from "../../components/Expertise/Expertise";
import Panel from "../../components/Panel/Panel";
import Address from "../../components/Address/Address";
import Feedback from "../../components/Feedback/Feedback";
import ButtonSmall from "./../../components/ButtonSmall/ButtonSmall";
import Portfolio from "../../components/Portfolio/Portfolio";
import Skills from "../../components/Skills/Skills";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronRight } from "@fortawesome/free-solid-svg-icons";
import data from "../../utils/data";
import Education from "../../components/Education/Education";

const Inner = () => {
  const [isOpen, setIsOpen] = useState(true);

  const togglePanel = () => {
    setIsOpen(!isOpen);
  };

  const scrollUp = () => window.scrollTo({ top: 0, behavior: "smooth" });

  return (
    <div className="innerPage">
      <Panel isOpen={isOpen} toggle={togglePanel} />
      <main
        className={`main__content ${isOpen ? "narrow" : "wide"} `}
        data-testid="inner"
      >
        <Box title="About me" id="about">
         Front-End developer with an engineering background in field of
          automation and telemechanic. I have hands-on experience of development
          top-industry standard projects in a full-cycle development team.
          Passionate in development high reliable, scalable and maintainable
          engineering systems.
           I am enthusiastic and experienced engineer who decided to change her career path and discover the world of new opportunities!
        </Box>
        <Box title="Education" id="education">
          <Education />
        </Box>
        <Box title="Experience" id="experience">
          <Expertise data={data.expertise} />
        </Box>
        <Box title="Skills" id="skills">
          <Skills />
        </Box>
        <Box title="Portfolio" id="portfolio">
          <Portfolio />
        </Box>
        <Box title="Contacts" id="contacts">
          <Address contactData={data.contactData} />
        </Box>
        <Box title="Feedbacks" id="feedbacks">
          <Feedback data={data.feedbacks} />
        </Box>
        <div className="scrollUp">
          <ButtonSmall clickHandler={scrollUp}>
            <FontAwesomeIcon icon={faChevronRight} />
          </ButtonSmall>
        </div>
      </main>
    </div>
  );
};

export default Inner;
